import React, { useEffect, useState } from "react";
import Layout from '../layout/Layout';
import Blog from '../components/Blog';
import { Helmet } from 'react-helmet';
import { BlogGetById } from "../services/BlogService";
import { globalHistory } from "@reach/router";

export default function BlogPage({ id }) {
  console.log(id);
  console.log(globalHistory.location.pathname);
  console.log(globalHistory.location);
  ///const blogId = "20210312064310"  
  var blogId = undefined;
  if(globalHistory.location.hash !== undefined){
    blogId = globalHistory.location.hash.replace("#",'');
  }
  
  return (
    <Layout>
      { (blogId) === undefined ?        
        (<h1>Blog Not Found.</h1>)
        : (<Blog blogId={blogId} />)
      }
      {/* <h1>This is {blogId}</h1> */}
      <Helmet>
        <title>Blogs | Bipul Raman</title>
      </Helmet>
    </Layout>
  );
}